@use 'base/variables' as v;
@use 'base/mixins' as m;

.summary{
    background-image: linear-gradient(to right, rgba(v.$blue, .7), rgba(v.$blue, .9)), url(../img/bg_numeros.jpg);
    background-image: linear-gradient(to right, rgba(v.$blue, .7), rgba(v.$blue, .9)), image-set(
        url('../img/bg_numeros.avif') 1x,
        url('../img/bg_numeros.webp') 1x,
        url('../img/bg_numeros.jpg') 1x
    );
    padding: 12rem 0;
    background-size: cover;
    background-position: center;

    &__grid{
        @include m.container;
        @include m.grid(1,5rem);
        @include m.tablet{
            @include m.grid(2, 5rem);
        }
    }

    &__block{
        text-align: center;
        color: v.$white;
    }
    &__text{
        font-weight: v.$bolder;
        line-height: 1;
        margin: 0;
        font-size: 2.4rem;
        text-transform: uppercase;
        &--number{
            font-size: 8rem;
        }
    }
}