@use 'base/variables' as v;
@use 'base/mixins' as m;

.devwebcamp{
    &__grid{
        @include m.container;
        @include m.grid(1,1rem);
        @include m.tablet {
            @include m.grid(2,5rem);
            align-items: center;
        }
    }
    &__text{
        line-height: 1.5;
    }
}